.page-footer {
	position: relative;
	padding-top: 26px;
	padding-bottom: 26px;
	background-color: #333333;
}


.footer-flex {
   display: flex;
   justify-content: space-between;

   @include max-w(940px) {
      flex-direction: column;
   } 
}



/*------------------.footer__left----------------------*/



.footer__left {
	display: flex;
   justify-content: space-between;
   width: 540px;

   @include max-w(940px) {
      width: 100%;
      margin-bottom: 30px;
   }

   @include max-w(767px) {
      flex-direction: column;
      align-items: center;
   }
}


.footer__contact {
   width: 121px;

   @include max-w(767px) {
      order: 2;
   }

	span {
		display: block;
	   color: #ffffff;
	   font-size: 12px;
	   line-height: 18px;
	}
}

.footer__menu {
	width: 340px;

	@include max-w(800px) {
      width: 70%;
   }

   @include max-w(767px) {
      width: 100%;
      margin-bottom: 25px;
   }

	ul {
      display: flex;
      justify-content: space-between;
      align-items: center;

      @include max-w(474px) {
         flex-direction: column;
      }

		li {

			@include max-w(474px) {
            display: block;
            width: 30%;
            margin-bottom: 5px;
            text-align: center;
         }
			
			a {
				display: block;
            color: #ffffff;
            font-size: 14px;
            transition: 0.5s;
			}

			a:hover {
				color: #31ad01;
			}
		}
	}
}

.menu-home {
	width: 15px;
   height: 13px;
	background-image: url(../images/menu-home-icon.png);
   background-repeat: no-repeat;
   background-position: 0 0;

   @include max-w(474px) {
      width: 100%;
      margin-bottom: 5px;
      background-position: center;
   }
}



/*-------------------.footer__right ------------------------------*/



.footer__right {
   width: 288px;

   @include max-w(940px) {
      width: 100%;
   }
}

.payment-systems {

	ul {
		display: flex;

		@include max-w(940px) {
         justify-content: center;
      }

      @include max-w(767px) {
         justify-content: space-around;
      }

		li {
			a {
				display: block;
				width: 50px;
				height: 34px;
				margin-left: 5px;
				border-radius: 4px;

				@include max-w(767px) {
               margin-left: 0px;
            }			
			}
		}
	}
}


.payment-systems-icon-1 {
   background-image: url(../images/payment-systems-icons.png);
   background-position: 0 0;
}

.payment-systems-icon-2 {
   background-image: url(../images/payment-systems-icons.png);
   background-position: -58px 0;
}
.payment-systems-icon-3 {
   background-image: url(../images/payment-systems-icons.png);
   background-position: -117px 0;
}
.payment-systems-icon-4 {
   background-image: url(../images/payment-systems-icons.png);
   background-position: 109px 0;
}
.payment-systems-icon-5 {
   background-image: url(../images/payment-systems-icons.png);
   background-position: 51px 0;
}
