// Кнопки

button {
  outline: none;
  border: none;
  padding-left: 0;
  padding-right: 0;
}

.btn-search {
  display: block;
}

.btn-search-header {
  width: 53px;
  height: 36px;
  padding-left: 0;
  padding-right: 0;
  background-color: #fff;
  background-image: url(../images/search-header.png);
  background-repeat: no-repeat;
  background-position: center right 18px;
  border-radius: 4px;
}

.btn-project {
  display: block;
  width: 105px;
  height: 30px;
  padding-left: 19px;
  border: 1px solid #144c7d;
  background-color: #ffffff;
  background-image: url(../images/bg-btn-project.png);
  background-repeat: no-repeat;
  background-position: center left 10px;
  color: #1b4d7c;
  font-family: $font-family-HelveticBold;
  font-size: 12px;
  text-transform: uppercase;
  border-radius: 4px;
  transition: 0.2s;

  span {
    display: block;
    margin-top: -1px;
  }
}


.btn-project:hover {
  background-image: url(../images/bg-btn-project-hover.jpg);
  background-color: #1b4d7c;
  color: #fff;
}


.btn-search-jobs {
  width: 61px;
  height: 36px;
  padding-left: 0;
  padding-right: 0;
  background-color: #00b329;
  background-image: url(../images/search.png);
  background-repeat: no-repeat;
  background-position: center;
  border-radius: 4px;
  transition: 0.5s;
}


.btn-search-jobs:hover {
  background-color: #258d00;
}


.btn-presentation {
  width: 141px;
  height: 36px;
  color: #ffffff;
  font-family: $font-family-HelveticBold;
  font-size: 14px;
  line-height: 30px;
  background-color: #00b22d;
  border-radius: 4px;
  transition: 0.5s;

  @include max-w(500px) {
    margin: 0 auto;
  }
}


.btn-presentation:hover {
  background-color: #258d00;
}



/*-------------------burger-menu------------------*/



.burger-menu {
  display: none;
  position: relative;
  cursor: pointer;

  @include max-w(940px) {
    display: block;
    z-index: 4;
  }

  @include max-w(671px) {
    position: relative;
    width: 35px;
    top: -101px;
    margin-left: auto;
    margin-right: 0;
  }  
}


.bg-bar {
  background-color: #fff!important;
}


.bar1, .bar2, .bar3 {
  width: 35px;
  height: 5px;
  background-color: #1b4d7c;
  margin: 6px 0;
  transition: 0.4s;
}


/* Rotate first bar */
.change .bar1 {
  -webkit-transform: rotate(-45deg) translate(-9px, 6px) ;
  transform: rotate(-45deg) translate(-8px, 7px) ;
}


/* Fade out the second bar */
.change .bar2 {
  opacity: 0;
}


/* Rotate last bar */
.change .bar3 {
  -webkit-transform: rotate(45deg) translate(-8px, -8px) ;
  transform: rotate(45deg) translate(-8px, -8px) ;
}
