//  Базовые стили

html {
  box-sizing: border-box;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
	background-color: $body-bg;
  color: $text-color;
	font-family: $font-family-HelveticaRegular;
  font-weight: $font-weight;
	font-size: $font-size;
	line-height: $line-height;
}


img {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
}

section {
  position: relative;
}

.page-container {
  max-width: $container-width;
  margin-left: auto;
  margin-right: auto;
  padding-left: $container-padding;
  padding-right: $container-padding;

  @include max-w(767px) {
    padding-left: 15px;
    padding-right: 15px;
  }

  @include max-w(416px) {
    padding-left: 10px;
    padding-right: 10px;
  }
}

.icon {
  display: inline-block;
}

ul {
  padding-left: 0;
  margin: 0;
}

li {
  list-style-type: none;
}

a { 
  text-decoration: none; 
}

p {
  margin: 0;
}

.marg-bot-20 {
  margin-bottom: 20px;
}

.title-3 {
  font-size: 16px;
  font-weight: 700;
  line-height: 24px;
  text-transform: uppercase;
  font-family: $font-family-HelveticBold;
}

.title-2 {
  font-family: $font-family-HelveticBold;
  font-size: 20px;
  line-height: 30px;
  text-transform: uppercase;
}