input[type='search'] {
  display: block;
  width: 100%;
  border: none;
  outline: none;
  padding-left: 10px;
  border-radius: 4px;
}

.search-header-width {
  width: 400px;

  @include max-w(671px) {
    width: 100%;
  }
}

.form-header {
  border: 1px solid #d5d4d4;
  border-radius: 4px;
  transition: 0.5s;
}

.form-header:hover {
  border: 1px solid #c1e1fe;
}

.form-header_flex {
  display: flex;
	justify-content: space-between;
}



//placeholder color

::-webkit-input-placeholder {
	color: #9b9b9b;
	font-size: 14px;
	line-height: 30px;
}

::-moz-placeholder {
	color: #9b9b9b;
	font-size: 14px;
	line-height: 30px;	
}/* Firefox 19+ */

:-moz-placeholder {
	color: #9b9b9b;
	font-size: 14px;
	line-height: 30px;
}/* Firefox 18- */

:-ms-input-placeholder {
	color: #9b9b9b;
	font-size: 14px;
	line-height: 30px;
}

select {
	-webkit-appearance: none!important;
  -moz-appearance: none!important;
  appearance: none!important;
	background-image: url(../images/arrow.png);
  background-position: right 8px center;
  background-repeat: no-repeat;
}


//for IE
select::-ms-expand {
  display: none;
}

@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
	
  select {
  	-webkit-appearance: none!important;
	  -moz-appearance: none!important;
	  appearance: none!important;
		background-image: url(../images/arrow.png);
	  background-position: right 8px center;
	  background-repeat: no-repeat;
  }
}
