.page-wrapper {
	padding-top: 10px;
	background-color: #f6f6f6;

	&_flex {
		display: flex;
		justify-content: space-between;

		@include max-w(940px) {
      flex-direction: column;
      align-items: center;
    } 
	}
}



/*---------------.site-bar-----------------------*/



.site-bar {
	width: 370px;

	@include max-w(1229px) {
    margin-right: 20px;
  }

  @include max-w(940px) {
    order: 1;
    margin-bottom: 30px;
    margin-right: 0;
  } 

  @include max-w(767px) {
    width: 100%;
  } 
}

.specialist {
	padding-top: 20px;
	padding-bottom: 20px;
	margin-bottom: 20px;
	background-color: #ffffff;

	&__foto {
		width: 130px;
		margin: 0 auto;
	}

	&__info {
		padding-top: 4px;

		a, span {
			display: block;
			color: #262626;
			font-family: $font-family-HelveticBold;
			font-size: 16px;
			line-height: 24px;
			text-align: center;
		}

    ul {
      display: flex;
      justify-content: center;
      width: 125px;
      margin: 0 auto;
      padding-top: 4px;

       	li {
       		display: block;
          width: 16px;
          height: 15px;
       	}
    }

    .motto {
      display: block;
      width: 170px;
      margin: 10px auto 0;
      padding-top: 7px;
      padding-bottom: 7px;
			border: 1px solid #d5d4d4;
			border-radius: 4px;
			background-color: #f6f6f6;
			color: #262626;
			font-size: 14px;
			line-height: 24px;
    }
	}
}


.star-img {
	background-image: url(../images/star.png);
  background-repeat: no-repeat;
  background-position: center right 0px;
}



/*---------------.communications---------------------*/




.communications {
	padding-top: 13px;
	padding-bottom: 13px;
	margin-bottom: 20px;
	background-color: #ffffff;

	ul {
      display: flex;
      flex-direction: column;
      align-items: center;

		li {
			width: 220px;
			padding-left: 40px;
			margin-bottom: 6px;

			a {
	      color: #1b4d7c;
	      font-family: $font-family-Arial;
	      line-height: 29px;
	      transition: 0.5s;
			}

			a:hover {
				color: #00b22d;
			}
		}		
	}
}


.selling-icon {
	background-image: url(../images/selling-icon.png);
  background-repeat: no-repeat;
  background-position: 1px 2px;
  border-bottom: 1px solid #d0d3d4;
}

.post-icon {
	background-image: url(../images/post-icon.png);
  background-repeat: no-repeat;
  background-position: 1px 2px;
  border-bottom: 1px solid #d0d3d4;
}

.home-icon {
	background-image: url(../images/home-icon.png);
  background-repeat: no-repeat;
  background-position: 1px 2px;
  border-bottom: 1px solid #d0d3d4;
}

.message-icon {
	margin-bottom: 0;
	background-image: url(../images/message-icon.png);
  background-repeat: no-repeat;
  background-position: 1px 7px;
  border-bottom: none;
}



/*------------------.invitation------------------------------*/



.invitation {
	padding-top: 19px;
	padding-bottom: 22px;
  background-color: #ffffff;

  .title-3 {
   	margin-bottom: 7px;
    text-align: center;
   }

  p {
   	margin: 0 auto 13px;
   	padding-right: 25px;
    padding-left: 30px;
	  color: #262626;
	  font-size: 14px;
	  line-height: 20px;
  }
}

.social-networks {

	ul {
		display: flex;
		justify-content: space-between;
		width: 228px;
		margin: 0 auto 19px;

		li {
			display: block;

			a {
			  display: block;
			  width: 30px;
			  height: 30px;		
			}
		}
	}
}


.vc-icon {
  background-image: url(../images/soc-icons.png);
  background-repeat: no-repeat;
  background-position: 0 0;
}


.fb-icon {
  background-image: url(../images/soc-icons.png);
  background-repeat: no-repeat;
  background-position: -32px 0;
}


.ok-icon {
  background-image: url(../images/soc-icons.png);
  background-repeat: no-repeat;
  background-position: 0 -32px;
}


.my-world-icon {
  background-image: url(../images/soc-icons.png);
  background-repeat: no-repeat;
  background-position: -32px -32px;
}


.google-icon {
  background-image: url(../images/soc-icons.png);
  background-repeat: no-repeat;
  background-position: -64px 0;
}


.twitter-icon {
  background-image: url(../images/soc-icons.png);
  background-repeat: no-repeat;
  background-position: -64px -32px;
}


.link-company {
	display: block;
	width: 228px;
	margin: 0 auto;
	padding-top: 9px;
	padding-bottom: 9px;
	border: 1px solid #b9b9b9;
   background-color: #ebebeb;
   color: #262626;
   font-family: $font-family-Arial;
   line-height: 20px;
   text-align: right;
}




/*---------------.page-content-----------------------------*/



.page-content {
	max-width: 770px;
	padding-bottom: 50px;

	@include max-w(940px) {
    order: 2;
    max-width: 100%;
  } 
}

.list-jobs {
	padding-left: 28px;
	padding-right: 30px;
	padding-top: 30px;
	padding-bottom: 30px;
	margin-bottom: 30px;
	background-color: #fff;

	.title-2 {
		margin-bottom: 19px;
		border-bottom: 1px solid #9b9b9b;
	}

	.search-header__field {
		padding-left: 20px;

		@include max-w(1147px) {
      padding-left: 4px;
    }
	}

	.list-jobs-form {
		margin-bottom: 10px;
	}
}



/*--------------.choose---------------------------*/


.choose-form {
	display: flex;
	justify-content: space-between;
	align-items: flex-end;
	flex-wrap: wrap;

	@include max-w(1147px) {
    flex-direction: column;
    lign-items: flex-start;
   }

	.btn-project {
		height: 36px;
	}
}

.choose-select {

	@include max-w(940px) {
    width: 100%;
   } 

	span {
		display: block;
    color: #9b9b9b;
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
	}

	select {
		width: 269px;
		height: 36px;
		padding-left: 4px;
		border: 1px solid #d5d4d4;
		border-radius: 4px;
		color: #9b9b9b;
		font-size: 14px;
		outline: none;

	   @include max-w(1147px) {
        margin-bottom: 20px;
      }

      @include max-w(940px) {
        width: 100%;
      } 
	}
}



/*---------------.presentations--------------------------*/


.presentations-item {
  padding-left: 31px;
	padding-top: 10px;
	padding-bottom: 18px;
	margin-bottom: 30px;
	background-color: #fff;

	&__header {
    display: flex;
    justify-content: space-between;
    padding-right: 31px;

    @include max-w(370px) {
      flex-direction: column;
    } 
     
    span { 
			color: #1b4d7c;
			font-size: 14px;
			line-height: 30px;
			font-weight: 600;
    }

    time {
			color: #9b9b9b;
			font-size: 14px;
			font-weight: 400;
			line-height: 32px;
    }
	}

	&__content {
		padding-right: 47px;
		padding-bottom: 13px;

		p {
			padding-top: 4px;
      line-height: 24px;
		}
	}
}

.presentations-item__footer {
	display: flex;
  justify-content: space-between;
	margin-right: 31px;
	padding-top: 11px;
	border-top: 1px solid #9b9b9b;

	@include max-w(500px) {
   	flex-direction: column;
  }
}

.views {
	width: 300px;
	display: flex;
	align-items: center;

	@include max-w(500px) {
   	width: 100%;
   	margin-bottom: 15px;
  }

	a {
		display: block;
		width: 140px;
		margin-left: 10px;
    color: #0595c5;
    font-size: 14px;
    line-height: 30px;
    text-decoration: underline;
	}

	span {
		display: block;
		width: 80px;
    color: #9b9b9b;
    padding-left: 30px;
		font-size: 14px;
		line-height: 24px;
		background-image: url(../images/view-icon.png);
    background-repeat: no-repeat;
    background-position: center left 0;
    text-align: left;

    @include max-w(500px) {
   	  margin-right: 	0;
   	  margin-left: auto;
    }
	}
}



/*--------------------.pagination------------------------*/



.pagination {

	ul {
    display: flex;

		li {
      display: block;
      margin-right: 8px;

			a {
				display: block;
        width: 30px;
        height: 30px;
        background-color: #f6f6f6;
        font-family: $font-family-HelveticBold;
				color: #004c7f;
				font-size: 16px;
				line-height: 30px;
				border: 1px solid #004b80;
				text-align: center;
				transition: 0.5s;
			}

			a:hover {
				color: #fff;
				background-color: #004c7f;
			}
		}
	}
}
