.page-header {
  position: relative;
  padding-top: 17px;
}


.header-top_flex {
	display: flex;
	justify-content: space-between;

	@include max-w(940px) {
      flex-direction: column;
   }
}


.header-top {
   padding-bottom: 17px;

	.logo {
      max-width: 130px;

      @include max-w(1144px) {
		   margin-right: 20px;
      } 

      @include max-w(671px) {
        margin-bottom: 20px;
      } 
	}
}


.header-top__left {
	width: 600px;
	display: flex;
	justify-content: space-between;

	@include max-w(940px) {
     width: 100%;
   }

   @include max-w(671px) {
      flex-direction: column;
   }  
}


.header-top__right {
	width: 470px;
	max-width: 470px;
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding-left: 8px;

	@include max-w(1144px) {
		margin-left: 30px;
   } 

   @include max-w(1088px) {
      margin-left: 30px;
   }

   @include max-w(940px) {
   	width: 100%;
   	max-width: 100%;
      margin-left: 0;
      margin-top: 20px;
      padding-left: 0;
   }

   @include max-w(669px) {
      margin-top: 0;
   } 
 
	.header-top__options {
		display: flex;
		justify-content: space-between;
		width: 100px;

		span {
         display: block;
			color: #9b9b9b;
			font-size: 14px;
		}

		.currency {
			padding-right: 17px;
			text-transform: uppercase;
			background-image: url(../images/bg-eur.png);
         background-repeat: no-repeat;
         background-position: center right 0px;
		}
	}
}


.header-bottom {
	background-color: #1b4d7c;

	@include max-w(940px) {
   	position: absolute;
      top: 0;
      right: 0;
      width: 300px;
      z-index: 3;
   }

   @include max-w(375px) {
   	width: 100%;
   } 
}

.header-menu {

	@include max-w(940px) {
   	display: none;
   }

	ul {
		display: flex;
		justify-content: space-between;
		padding-left: 0;
		margin: 0;

		@include max-w(940px) {
   	   flex-direction: column;
   	   z-index: 2;
   	   padding-bottom: 30px;
   	   padding-top: 60px;
      } 

		li {
			position: relative;
			display: block;
			padding-top: 2px;
			padding-bottom: 2px;

			a {
				color: #ffffff;
				line-height: 36px;

				@include max-w(1024px) {
              font-size: 14px;
            }
			}
		}

		li:hover:after {
			position: absolute;
			content: "";
			display: block;
			height: 3px;
			width: 100%;
			background-color: #fff;	
			bottom: 0px;
		}
	}
}